import { Component, Input, OnInit, OnChanges} from '@angular/core';
import { Coracoes } from 'app/shared/Coracoes.model';
@Component({
  selector: 'app-tentativas',
  templateUrl: './tentativas.component.html',
  styleUrls: ['./tentativas.component.css']
})
export class TentativasComponent implements OnInit,OnChanges {
  @Input() public tentativas:number
 public varios:Array<Coracoes>=[];
 public init:boolean = false
  constructor() { }

  ngOnChanges() {
    if(this.tentativas!==this.varios.length&&this.init&&this.tentativas>-1){
      this.varios[(this.varios.length-1)-this.tentativas]['is_co']=false
    }

      
  }
  
  ngOnInit() {
    for(let l:number=0;l<this.tentativas;l++){
      this.varios.push(new Coracoes(true))
    }
    this.init=true
  }
  
}



