import { Component } from '@angular/core';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent {
  public jogoRolando:boolean=true;
  public quem:string
  public emitter(ev:string):void{
    this.jogoRolando=false
    this.quem=ev
  }
  public reinicar(){
    this.quem=undefined
    this.jogoRolando=true;
    location.reload()
  }
}



