import { Component, OnInit, OnDestroy ,Output, EventEmitter} from '@angular/core';
import {Frase} from '../shared/Frase.model';
import {FRASES} from './frases.mock';

@Component({
  selector: 'app-painel',
  templateUrl: './painel.component.html',
  styleUrls: ['./painel.component.css']
})
export class PainelComponent implements OnInit, OnDestroy {
  public  frases: Array<Frase> = FRASES;
  public fraseEng: string ='';//
  public resposta: string ='';
  public tentativas:number=3
  public progresso:number=0
  public v:number
  @Output() public emitter:EventEmitter<string>= new EventEmitter();
  constructor() {
    this.sorteio();
  }
  public verefica(frase:string){
    return frase.toLocaleLowerCase()==this.resposta.toLocaleLowerCase()?true:false
  }
  public win(){
    this.frases=[]
    this.frases=FRASES;
    setTimeout(()=>{
      this.emitter.emit("vitoria")
    },1000)
  }
  public lose(){
    this.emitter.emit("derrota")
  }
  public impFrase():void{
    if(this.frases.length>0){
      this.fraseEng=this.frases[this.v].fraseIngles;
      if(this.verefica(this.frases[this.v].frasePortugues)){
        this.frases.splice(this.v,1)
        this.progresso+=25
      }else this.tentativas--
      this.sorteio();
    }
    else{
      this.fraseEng="Acabou as frases"
    }
  }
  public words(word:Event):void{
   this.resposta= (<HTMLInputElement>word.target).value;
  }
  public sorteio(){
    if(this.progresso>=100)this.win()
    else if(this.tentativas<0) this.lose()
    else{
      this.v=this.getRadom(0, this.frases.length)
      this.fraseEng =  this.frases[this.v].fraseIngles
    }
  }
  public getRadom(min:number, max:number):number {
    return Math.floor( Math.random() * (max - min) + min);
  }
  ngOnInit() {
    
  }
  ngOnDestroy(){
    //console.log(FRASES)//Não consigo reuperar essa constante para estado inicial sem realizar reload da página
  }

}



